/* m-plus-rounded-1c-regular - latin-ext_latin_hebrew */
@font-face {
  font-family: 'M PLUS Rounded 1c';
  font-style: normal;
  font-weight: 400;
  src: url('/fonts/m-plus-rounded-1c-v9-latin-ext_latin_hebrew-regular.eot');
  /* IE9 Compat Modes */
  src: local('M PLUS Rounded 1c'), local('MPLUSRounded1c-Regular'),
    url('/fonts/m-plus-rounded-1c-v9-latin-ext_latin_hebrew-regular.eot?#iefix') format('embedded-opentype'),
    /* IE6-IE8 */
    url('/fonts/m-plus-rounded-1c-v9-latin-ext_latin_hebrew-regular.woff2') format('woff2'),
    /* Super Modern Browsers */
    url('/fonts/m-plus-rounded-1c-v9-latin-ext_latin_hebrew-regular.woff') format('woff'),
    /* Modern Browsers */
    url('/fonts/m-plus-rounded-1c-v9-latin-ext_latin_hebrew-regular.ttf') format('truetype'),
    /* Safari, Android, iOS */
    url('/fonts/m-plus-rounded-1c-v9-latin-ext_latin_hebrew-regular.svg#MPLUSRounded1c') format('svg');
  /* Legacy iOS */
}

