.langChanger {
  position: fixed;
  bottom: 1rem;
  left: 1rem;
  border: 1px solid var(--colorGrey);
  border-radius: 4px;
  background: none;
  font: var(--textFormField);
  padding: 5px 8px;
  z-index: 10;
  user-select: none;

  &:focus {
    border: none;
    border: 1px solid var(--colorPrimary);
  }
}

/// Message, shown on the form
/// @group Login
.message {
  width: 100%;
  height: auto;
  font-size: 1rem;
  transition: var(--boxTransition);
}

/// Message, hidden from view
/// @group Login
.messageHidden {
  @extend .message;
}